import { SharedService } from './shared.service';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.css'],
  providers: [ SharedService ]
})
export class AppComponent {
  constructor(private shared: SharedService) {}
}



